@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
html {
  box-sizing: border-box;
  font-family: sans-serif;
  font-size: 16px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  background-color: #0f3460;
  color: #eeeeee;
  font-family: "Lato", sans-serif;
}

ul {
  list-style: none;
}

a {
  color: #eeeeee;
  text-decoration: none;
}

a:visited {
  color: #eeeeee;
}

img {
  width: 100%;
  height: auto;
}

.features-bar {
  min-height: 10vh;
  display: flex;
  z-index: 1;
}

.features-bar__features-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-grow: 2;
}

.toggle-sidebar {
  min-width: 35px;
  height: 35px;
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.5rem;
  background-color: #e94560;
  color: #eeeeee;
  z-index: 1;
}

.features-bar__search {
  align-self: center;
  outline: 1px solid grey;
  margin: 0 auto;
}

.features-bar__search input {
  width: 64vw;
  height: 35px;
  padding-left: 1rem;
  border: none;
}

.features-bar__search button {
  width: 50px;
  height: 35px;
  line-height: 35px;
  border: none;
  background: #e94560;
}

/* Layout for mobile devices*/
@media screen and (max-width: 480px) {
  .features-bar__features-list {
    min-width: 100vw;
    min-height: 100vh;
    display: none;
    flex-direction: column;
    justify-content: space-around;
    z-index: 1;
    background-color: #16213e;
    font-size: 2rem;
  }

  .features-bar__item {
    display: block;
    min-height: 50px;
    min-width: 50px;
    text-align: center;
  }

  .features-bar__item select {
    min-width: 200px;
    font-size: 1.5rem;
  }

  .toggle-sidebar {
    display: grid;
    place-content: center;
  }
}

/* Layout for tablets*/
@media screen and (min-width: 481px) and (max-width: 1023px) {
  .features-bar {
    flex-wrap: wrap;
  }

  .features-bar__item select {
    width: 70px;
  }
}

/* Layout for laptops and desktop */
@media screen and (min-width: 1024px) {
  .features-bar {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }
  .features-bar__search {
    margin-right: 1rem;
  }

  .features-bar__search input {
    width: 25vw;
  }
}

/* Bigger screens layout */
@media screen and (min-width: 1441px) {
  .features-bar,
  .features-bar__item select {
    font-size: 2rem;
  }

  .features-bar__search {
    height: 50px;
  }

  .features-bar__search input,
  .features-bar__search button {
    height: 50px;
    font-size: 2rem;
  }
}

.features-bar__search--hide {
  display: none;
}

.features-bar__features-list--sidebar {
  display: flex;
}

/*Look and feel*/
@media screen and (min-width: 801px) {
  .features-bar {
    background-color: #16213e;
  }
}

.features-bar__search input::-webkit-input-placeholder {
  color: #000;
}

.features-bar__search input:-ms-input-placeholder {
  color: #000;
}

.features-bar__search input::-ms-input-placeholder {
  color: #000;
}

.features-bar__search input::placeholder {
  color: #000;
}

.loading-movie {
  min-height: 100%;
  background-color: #e94560;
}

.movie-card {
  background-color: #e94560;
}

.movie-card__image {
  min-height: 78%;
  display: grid;
}

.movie-card__image img {
  align-self: stretch;
  object-fit: cover;
}

.movie-card__title {
  min-height: 22%;
  position: relative;
}

.movie-card__title a {
  min-height: 100%;
  display: inline-block;
  padding: 0.5rem;
  font-size: 1.5rem;
  font-weight: bold;
}

@media screen and (max-width: 480px) {
  .movie-card {
    width: 300px;
  }
}

@media screen and (min-width: 1441px) {
  .movie-card__title a {
    font-size: 2rem;
  }
}

.home {
  min-height: 100vh;
}

.home__movies {
  min-height: 90vh;
  display: grid;
  grid-gap: 40px 20px;
  gap: 40px 20px;
  padding: 1rem;
}

.home__title {
  padding: 1rem;
  text-align: center;
}

/* Layout for mobile devices*/
@media screen and (max-width: 480px) {
  .home__movies {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

/* Layout for tablets*/
@media screen and (min-width: 481px) and (max-width: 800px) {
  .home__movies {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Layout for laptops and desktop */
@media screen and (min-width: 801px) {
  .home__movies {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media screen and (min-width: 1441px) {
  .home__title {
    font-size: 4rem;
  }
}

.lists-menu {
  display: inline-block;
  margin-left: 1rem;
  position: relative;
}

.lists-menu__lists {
  display: none;
  min-width: 250px;
  padding: 1rem;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #f1f1f1;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  font-size: 1rem;
}

.list-menu__item {
  min-height: 30px;
  display: block;
  padding: 0.5rem;
}

.list-menu__item input {
  height: 25px;
  padding: 0.5rem;
  border: 1px solid black;
}

/*Layout for mobile devices and tablets*/
@media screen and (max-width: 800px) {
  .list-icon {
    font-size: 1.5rem;
  }
}

/*Layout for laptops and desktop*/
@media screen and (min-width: 801px) {
  .lists-menu {
    cursor: pointer;
  }

  .list-icon {
    font-size: 2rem;
  }
}

/*Layout for bigger screens*/
@media screen and (min-width: 1441px) {
  .list-icon {
    font-size: 3rem;
  }

  .lists-menu__lists {
    min-width: 300px;
    font-size: 2rem;
  }

  .list-menu__item {
    height: 50px;
  }

  .list-menu__item input {
    height: 45px;
    font-size: 1.5rem;
  }
}

.lists-menu:hover .lists-menu__lists {
  display: block;
}

.list-menu__item a:hover,
.list-menu__item span:hover {
  color: #e94560;
  font-weight: bold;
}

/*Look and feel*/
.list-icon {
  color: #e94560;
}

.lists-menu__lists {
  background-color: #1a1a2e;
}

.list-menu__item input {
  background-color: #eeeeee;
}

.list-menu__item input::-webkit-input-placeholder {
  color: #000;
}

.list-menu__item input:-ms-input-placeholder {
  color: #000;
}

.list-menu__item input::-ms-input-placeholder {
  color: #000;
}

.list-menu__item input::placeholder {
  color: #000;
}

.movies-list {
  min-height: 100vh;
}

.movies-list__header {
  padding: 1rem;
}

.movies-list__title {
  margin-bottom: 0.5rem;
}

.movie-list__icons {
  margin-left: 1rem;
  color: #e94560;
  font-size: 2rem;
  cursor: pointer;
}

.movies-list__movies {
  display: grid;
  padding: 1rem;
  grid-gap: 40px;
  gap: 40px;
}

.movies-list__delete-icon {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;

  font-size: 1.5rem;
}

.movies-list__delete-icon:hover {
  color: #0f3460;
}

/* Layout for mobile devices */
@media screen and (max-width: 480px) {
  .movies-list__title {
    font-size: 1.5rem;
  }

  .movies-list__movies {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .movie-list__icons {
    font-size: 1.5rem;
  }
}

/* Layout for tablets*/
@media screen and (min-width: 481px) and (max-width: 800px) {
  .movies-list__header {
    justify-content: center;
  }

  .movies-list__movies {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 801px) {
  .movies-list__movies {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media screen and (min-width: 1441px) {
  .movies-list__title {
    font-size: 4rem;
  }

  .movie-list__icons {
    margin-left: 2rem;
    font-size: 3rem;
  }

  .movies-list__delete-icon {
    font-size: 2.5rem;
  }
}

.fetching-movie {
  min-height: 100vh;
  display: grid;
  place-content: center;
  font-size: 2rem;
}

.movie {
  min-height: 100vh;
}

.movie__poster-image {
  max-height: 100vh;
}

.movie__description {
  padding: 0 1rem;
}

.movie__overview {
  text-align: justify;
}

.movie__title,
.movie__similar-movies h3,
.youtube-container h2 {
  padding: 1rem;
}

.movie__genres {
  padding: 1rem;
}

.movie__genres li {
  width: 150px;
  padding: 0.5rem;
  display: inline-block;
  margin: 0rem 1rem 1rem 1rem;
  background-color: teal;
  font-size: 0.9rem;
  text-align: center;
}

.youtube-container {
  text-align: center;
}

.no-movies {
  text-align: center;
}

/* Layout for mobile devices  and tablets*/
@media screen and (max-width: 800px) {
  .movie__poster-image {
    display: none;
  }

  .movie__title {
    text-align: center;
  }

  .movie__genres {
    display: none;
  }

  .movie__similar-movies {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    gap: 10px;
  }

  .movie__similar-movies h3 {
    grid-column: 1 / 4;
  }

  .movie__similar-movies a:nth-child(11) {
    display: none;
  }

  .movie__trailer {
    width: 95%;
    height: 85%;
  }
}

/*Layout for tablets*/
@media screen and (min-width: 540px) and (max-width: 800px) {
  .movie {
    font-size: 1.5rem;
  }

  .movie__trailer {
    height: 300px;
  }
}

/* Layout for laptops and desktops */
@media screen and (min-width: 801px) {
  .movie {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .movie__poster {
    display: grid;
    grid-template-columns: 100%;
    place-content: center;
    padding: 1rem;
  }

  .movie__similar-movies {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
    gap: 10px;
  }

  .movie__similar-movies h3 {
    grid-column: 1 / 6;
    padding: 1rem;
    text-align: center;
  }

  .movie__title {
    min-height: 10vh;
    font-size: 2rem;
  }

  .movie__details {
    min-height: 90vh;
  }
}

@media screen and (min-width: 1441px) {
  .fetching-movie {
    font-size: 4rem;
  }

  .movie__title {
    font-size: 5rem;
  }

  .movie__overview {
    font-size: 2rem;
  }

  .movie__genres li {
    width: 200px;
    font-size: 2rem;
  }

  .youtube-container h2 {
    font-size: 4rem;
  }

  .movie__similar-movies h3 {
    font-size: 3rem;
  }

  .movie__trailer {
    width: 900px;
    height: 506px;
  }
}

